<template>
    <div>
        <mhead-top titleHead="logo" lfIcon="el-icon-s-home icon" lfUrl="/home" rgIcon="el-icon-user-solid icon" rgUrl="/userCenter/index/index"></mhead-top>
        <div class="mBg">
            <div class="regBox">
                <div class="register-panel">
                    <h2 class="tc">六摩尔</h2>
                    <h3 class="tc">sixmol</h3>
                    <el-form :model="form" :rules="rules" ref="form" label-width="70px" class="mt15">
                        <el-form-item label="手机号" prop="mobile">
                            <el-input v-model.number="form.mobile" placeholder="请输入手机号"></el-input>
                        </el-form-item>
                        <el-form-item label="验证码" prop="code">
                            <el-row >
                                <el-col :span="12">
                                    <el-input v-model.number="form.code" placeholder="请输入短信验证码"></el-input>
                                </el-col>
                                <el-col :offset="1" :span="11">
                                    <el-button type="primary" class="w" :disabled="computedTime?true:false" @click.prevent="getVerifyCode">{{computedTime?'已发送('+computedTime+'s)':'获取短信验证码'}}</el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                        <el-button type="primary" @click="submitForm('form')" class="w mt10">注册</el-button>
                        <p class="tr"><router-link class="color6" :to="{path:'/login/forgetkey',query:{}}">忘记密码？</router-link></p>
                    </el-form>
                </div>
                <div class="loginP_bottom tc">已有账号？<router-link class="colorGreen" :to="{path:'/login/login',query:{}}">登录</router-link></div>
            </div>
        </div>
    </div>


</template>


<script>
    import mheadTop from '../../components/mobile/headTop';
    import {getCode,phoneLogin} from '../../service/api'
    export default {
        name: 'mRegister',
        data(){
            return{
                form:{
                    mobile:'',
                    code:''
                },
                computedTime: 0, //倒数记时
                rules:{
                    mobile: [
                        { required: true, message: '请输入手机号'},
                        { type: 'number', message: '手机号必须为数字值'},
                        {
                            pattern: /^0{0,1}(13[0-9]|15[7-9]|153|156|18[7-9])[0-9]{8}$/,
                            message: '手机号格式不对',
                            trigger: 'blur'
                        }
                    ],
                    code:[
                        { required: true, message: '请输入短信验证码'},
                        { type: 'number', message: '验证码必须为数字值'}
                    ]
                }
            }
        },
        components:{
            mheadTop
        },
        mounted(){


        },
        computed:{


        },
        methods:{
            submitForm(formName) {
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        var res = await phoneLogin(this.form.mobile);
                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                        }
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //获取短信验证码
            async getVerifyCode(){
                if (this.form.mobile=='') {
                    this.$message({
                        message: '手机号码不能为空',
                        type: 'warning'
                    });
                }else if(!(/^1\d{10}$/gi.test(this.form.mobile))){
                    this.$message({
                        message: '手机格式不正确',
                        type: 'warning'
                    });
                }else{

                    this.computedTime = 30;
                    this.timer = setInterval(() => {
                        this.computedTime --;
                        if (this.computedTime == 0) {
                            clearInterval(this.timer)
                        }
                    }, 1000);

                    var res = await getCode(this.form.mobile);

                    if(res){
                        this.$message({
                            message: res.msg,
                            type: res.status==12000?'success':'warning'
                        });
                    }
                }

            },

        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*注册*/
    .regBox{
        width:px2rem(750);
        margin-top: px2rem(20);
    }
    .regBox h2,.regBox h3{font-size:28px;font-weight: bold;line-height: 36px;color:#0bb8b2;}

    .register-panel{
        background-color: #fff;
        padding:px2rem(20);
        flex-shrink: 0;
    }
    .loginT{font-size: 16px;text-align: center;width:50%;line-height: 40px;cursor: pointer;}
    .loginT.active{border-bottom: 3px solid #0bb8b2;}

    .loginP_bottom{
        background-color: #ddd;color:#333;
        line-height: 60px;
        position: fixed;
        bottom:0;
        left: 0;
        width:px2rem(750);
    }
    .loginP_bottom a:hover{text-decoration: underline;}


</style>



